/** @jsx jsx */
import { css, jsx } from '@emotion/core';
import { Link, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import { Fragment } from 'react';
import type { FluidObject, FixedObject } from 'gatsby-image';
import type { FC } from 'react';

import DevlogTemplateMeta from './devlog-template-meta';
import Layout from './layout';
import Seo from './seo';
import { rhythm } from './styles/typography';

type Parent = {
  excerpt: string;
};

type HeroImage = {
  sharp: {
    fluid: FluidObject;
    fixed: FixedObject;
  };
};

export type Devlog = {
  body: string;
  title: string;
  author: string;
  image: HeroImage;
  slug: string;
  date: string;
  updated: string;
  parent: Parent;
};

type DevlogTemplateProps = { data: { devlog: Devlog } };

export const query = graphql`
  query($pageID: String!) {
    devlog(id: { eq: $pageID }) {
      body
      title
      slug
      author
      date
      updated
      image {
        sharp: childImageSharp {
          fixed {
            ...GatsbyImageSharpFixed
          }
          fluid(maxWidth: 700, maxHeight: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      parent {
        ... on Mdx {
          excerpt(pruneLength: 110)
        }
      }
    }
  }
`;

const DevlogTemplate: FC<DevlogTemplateProps> = ({ data }) => {
  const {
    title,
    image,
    slug,
    body,
    date,
    updated,
    parent: { excerpt },
  } = data.devlog;

  return (
    <Fragment>
      <nav
        css={css`
          max-width: 37em;
          margin: ${rhythm(1.5)} auto;
          padding: 0 ${rhythm(0.5)};
        `}
      >
        <ul
          css={css`
            list-style: none;
            margin: 0;
            padding: 0;
          `}
        >
          <li>
            <Link to="/">&larr; back to home</Link>
          </li>
        </ul>
      </nav>

      <Layout>
        <Seo
          appendSiteTitleToDescription
          title={title}
          image={image.sharp.fixed.src}
          urlSlug={slug}
          description={excerpt}
        />

        <article
          css={css`
            > :last-child {
              margin-bottom: 0;
            }
          `}
        >
          <h1>{title}</h1>

          <DevlogTemplateMeta date={date} updated={updated} />

          <Img
            css={css`
              margin-bottom: ${rhythm(1)};
              box-shadow: 0 0 6px rgba(0, 0, 0, 0.11), 0 0 0 1px rgba(0, 0, 0, 0.05);
              border-radius: 3px;
            `}
            fluid={image.sharp.fluid}
            alt={title}
          />

          <MDXRenderer>{body}</MDXRenderer>
        </article>
      </Layout>

      <nav
        css={css`
          max-width: 37em;
          margin: 0 auto ${rhythm(1.5)} auto;
          padding: 0 ${rhythm(0.5)};
        `}
      >
        <ul
          css={css`
            list-style: none;
            margin: 0;
            padding: 0;
          `}
        >
          <li>
            <Link to="/">&larr; back to home</Link>
          </li>
        </ul>
      </nav>
    </Fragment>
  );
};

export default DevlogTemplate;
